@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
* {
  box-sizing: border-box;
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
.body--scrolloff {
  overflow: hidden;
}
.standart-btn {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
  transition: 0.2s;
}
.standart-btn:disabled {
  opacity: 0.3;
}
.standart-btn:hover,
.standart-btn:focus {
  color: #ffffff;
  background-color: #a19d9d;
}
.standart-btn:active {
  opacity: 0.6;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.main {
  flex-grow: 1;
  display: grid;
  grid-template-rows: min-content min-content;
  grid-template-columns: 1fr;
  grid-row-gap: 20px;
  padding: 0 20px;
  background-color: #ededed;
  --breakpoint: 85%;
  --breakpoint-offset: 0.5px;
  background: linear-gradient(176deg, #ededed calc(var(--breakpoint) - var(--breakpoint-offset)), #f7f7f7 calc(var(--breakpoint) + var(--breakpoint-offset)));
}
.header {
  width: 100%;
  height: 300px;
  --breakpoint: 65%;
  --breakpoint-offset: 0.5px;
  background: linear-gradient(176deg, #f7f7f7 calc(var(--breakpoint) - var(--breakpoint-offset)), #ededed calc(var(--breakpoint) + var(--breakpoint-offset)));
}
.footer {
  height: 100px;
  width: 100%;
  background-color: #f7f7f7;
}
.cars-table {
  width: 100%;
}
@media (max-width: 900px) {
  .cars-table {
    overflow: auto;
    max-width: 100%;
    max-height: 600px;
  }
}
.add-block {
  align-self: start;
}
.table {
  background-color: #ffffff;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
}
@media (max-width: 900px) {
  .table {
    width: 860px;
  }
}
@media (max-width: 600px) {
  .table {
    width: 700px;
  }
}
.table__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  color: purple;
  margin-bottom: 15px;
}
@media (max-width: 900px) {
  .table__title {
    text-align: left;
  }
}
.table__head {
  background-color: #f0f2f2;
}
.table__body > .table__row {
  color: #000000;
}
.table__body > .table__row:nth-child(even) {
  background-color: #fcf5f5;
}
.table__body > .table__row:nth-child(odd) {
  background-color: #f7f2f2;
}
.table__item {
  text-align: center;
  vertical-align: baseline;
  padding: 20px;
  font-size: 18px;
  letter-spacing: 1px;
}
@media (max-width: 900px) {
  .table__item {
    font-size: 17px;
    padding: 15px;
  }
}
@media (max-width: 600px) {
  .table__item {
    font-size: 15px;
    padding: 10px;
  }
}
.table__item--head {
  color: #000000;
  font-size: 19px;
  font-weight: bold;
  text-transform: uppercase;
}
.table__item--car {
  font-size: 19px;
  background-color: #e6e1e1;
  color: #000000;
  text-transform: uppercase;
  word-wrap: break-word;
}
.table__item--buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 900px) {
  .table__item--buttons {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 15px;
  }
}
.table__item:nth-child(1) {
  width: 25%;
  text-align: left;
}
.table__item:nth-child(2) {
  width: 30%;
}
.table__item:nth-child(3) {
  width: 25%;
}
.table__item:nth-child(4) {
  text-align: right;
}
.table__button {
  width: 85px;
  padding: 10px 15px;
}
.table__button:nth-child(n+2) {
  margin-left: 10px;
}
.table__button:nth-child(n+3) {
  margin-top: 10px;
}
@media (max-width: 900px) {
  .table__button {
    justify-self: end;
  }
  .table__button:nth-child(n+2) {
    margin-left: 0;
  }
}
.table__sort-button {
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: none;
  padding: 5px;
  margin: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='11' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.5 10 0 0h11' fill='%23231F20'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
}
@media (max-width: 600px) {
  .table__sort-button {
    width: 20px;
    height: 20px;
  }
}
.table__sort-button--upp {
  transform: rotate(180deg);
}
.table__sort-button:hover,
.table__sort-button:focus {
  opacity: 0.8;
}
.table__sort-button:active {
  opacity: 0.5;
}
.add-block {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
@media (max-width: 900px) {
  .add-block {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .add-block {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
}
.add-block__input {
  width: 100%;
  margin: 0;
  padding: 10px 15px;
  border: none;
  border: 2px solid transparent;
}
.add-block__input--error {
  border-color: red;
  animation: shake 0.6s;
}
.add-block__input::placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  opacity: 0.8;
  color: #000000;
}
.add-block__input:disabled {
  background-color: rgba(0, 0, 0, 0.6);
}
.add-block__button {
  justify-self: end;
  width: 130px;
  padding: 10px 15px;
}
@media (max-width: 900px) {
  .add-block__button {
    width: 100%;
  }
}
@keyframes shake {
  0%,
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translateX(-7px);
    transform: translateX(-7px);
  }
  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translateX(7px);
    transform: translateX(7px);
  }
}
.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.modal__content {
  z-index: 1;
  width: 600px;
  height: 300px;
  overflow: auto;
  background-color: #ffffff;
  padding: 20px 15px;
}
.modal__form {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  grid-row-gap: 20px;
  justify-items: center;
}
.modal__input {
  width: 95%;
  border: none;
  background-color: #ededed;
  border: 2px solid transparent;
}
.modal__input--error {
  border-color: red;
  animation: shake 0.6s;
}
.modal__input::placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  opacity: 0.8;
  color: #000000;
}
.modal__input:disabled {
  background-color: rgba(0, 0, 0, 0.6);
}
.modal__button {
  width: 95%;
  background-color: #ededed;
  text-transform: uppercase;
}
.modal--hidden {
  visibility: hidden;
  opacity: 0;
}
@keyframes shake {
  0%,
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translateX(-7px);
    transform: translateX(-7px);
  }
  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translateX(7px);
    transform: translateX(7px);
  }
}
